<template>
    <div>
        <Dialog ref="dialog" />
        <CRow class="p-2">
            <CCol sm="9">
                <CInput
                    size="sm"
                    v-model="searchText"
                    v-on:keyup.enter="search"
                >
                    <template #prepend>
                        <CButton color="primary" size="sm" v-on:click="search">
                            <CIcon name="cil-magnifying-glass" size="sm" />
                        </CButton>
                    </template>
                </CInput>
            </CCol>
            <CCol sm="3" class="text-right">
                <CButton
                    color="primary"
                    size="sm"
                    v-if="$store.getters.isAdmin"
                    v-on:click="add"
                >
                    <CIcon name="cil-plus" size="sm" />
                </CButton>
                <CButton color="primary" size="sm" v-on:click="download">
                    <CIcon name="cil-cloud-download" size="sm" />
                </CButton>
                <CButton color="primary" size="sm" v-on:click="reload">
                    <CIcon name="cil-reload" size="sm" />
                </CButton>
            </CCol>
        </CRow>
        <CRow class="p-4">
            <template v-for="(col, i) in filters">
                <div style="float: right; margin-top: 8px" v-bind:key="i">
                    <v-menu :close-on-content-click="false">
                        <v-list flat dense class="pa-0">
                            <v-list-item-group multiple class="py-2">
                                <template v-for="(item, i) in col.data">
                                    <v-list-item
                                        :key="`item-${i}`"
                                        :value="item.key"
                                        :ripple="false"
                                    >
                                        <template>
                                            <v-list-item-action>
                                                <v-checkbox
                                                    v-model="item.checkbox"
                                                    :disabled="loading"
                                                    @click="
                                                        onCheckboxClicked(col)
                                                    "
                                                    color="primary"
                                                    dense
                                                ></v-checkbox>
                                            </v-list-item-action>
                                            <v-list-item-content>
                                                <v-list-item-title
                                                    v-text="item.name"
                                                ></v-list-item-title>
                                            </v-list-item-content>
                                        </template>
                                    </v-list-item>
                                </template>
                            </v-list-item-group>
                            <v-divider></v-divider>
                            <CButton class="m-2" color="primary" size="sm">
                                Toggle all
                            </CButton>
                        </v-list>
                    </v-menu>
                </div>
            </template>
        </CRow>
        <v-data-table
            class="elevation-1"
            :page="page"
            :pageCount="pageCount"
            :headers="headers"
            :items="items"
            :options.sync="options"
            :server-items-length="serverItemsLength"
            :loading="loading"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :footer-props="{
                disableItemsPerPage: disableItemsPerPage,
                disablePagination: disablePagination,
                showFirstLastPage: true,
                showCurrentPage: true,
                itemsPerPageOptions: [10, 20, 50, 100]
            }"
        >
            <template v-slot:[`item.name`]="{ item }">
                {{ item.name_tc }} {{ item.name_en }}
            </template>
            <template v-slot:[`item.created_at`]="{ item }">
                {{ item.created_at | moment("dddd, Do MMMM YYYY") }}
            </template>
            <template v-slot:[`item.updated_at`]="{ item }">
                {{ item.updated_at | moment("dddd, Do MMMM YYYY") }}
            </template>
            <template v-slot:[`item.actions`]="{ item }">
                <CButtonGroup>
                    <CButton
                        v-for="action in item.actions"
                        :key="action.key"
                        :color="action.color"
                        :disabled="action.disabled"
                        size="sm"
                        @click="click(item.id, action.type)"
                    >
                        {{ action.title }}
                    </CButton>
                </CButtonGroup>
            </template>
        </v-data-table>
    </div>
</template>
<script>
//
import { mapState } from "vuex";
import { Dialog } from "../../components";

export default {
    name: "Employee",
    props: {
        id: null
    },
    components: {
        Dialog
    },
    computed: {
        ...mapState(["company"]),
        serverItemsLength() {
            return this.company.data?.total;
        },
        pageCount() {
            return this.company.data?.last_page;
        },
        items() {
            return this.company.data?.data;
        }
    },
    data() {
        return {
            filters: {},
            activeFilters: {},
            searchText: null,
            page: 1,
            // serverItemsLength: 0,
            // pageCount: 0,
            // items: [],
            loading: false,
            options: {},
            sortBy: "created_at",
            sortDesc: false,
            disableItemsPerPage: false,
            disablePagination: false,
            headers: [
                { text: "Owner", value: "owner.name" },
                { text: "Name", value: "name" },
                { text: "No.", value: "number" },
                {
                    text: "DOI",
                    value: "incorporation_date"
                },
                { text: "Address", value: "address" },
                {
                    text: "RSC",
                    value: "registered_share_capital"
                },
                { text: "Created at", value: "created_at" },
                { text: "Updated at", value: "updated_at" },
                { text: "Actions", value: "actions" }
            ]
        };
    },
    watch: {
        options: {
            handler() {
                this.fetch();
            }
        },
        loading() {
            this.disableItemsPerPage = this.loading;
            this.disablePagination = this.loading;
        },
        searchText(val) {}
    },
    methods: {
        fetch() {
            let self = this;
            self.loading = true;
            const { page, itemsPerPage, sortBy, sortDesc } = self.options;
            let data = {
                page: page,
                per_page: itemsPerPage,
                sort_by: sortBy,
                sort_desc: sortDesc,
                search: self.searchText
            };
            this.$store
                .dispatch("company/get", data)
                .then(response => {
                    self.loading = false;
                })
                .catch(error => {
                    self.loading = false;
                });
        },
        search() {
            this.fetch();
        },
        add() {
            this.$router.push({ name: "CreateCompany" });
        },
        download() {
            let self = this;
            self.loading = true;
            const { page, itemsPerPage, sortBy, sortDesc } = self.options;
            let data = {
                page: page,
                per_page: itemsPerPage,
                sort_by: sortBy,
                sort_desc: sortDesc,
                search: self.searchText
            };
            this.$store
                .dispatch("company/export", data)
                .then(() => {
                    self.loading = false;
                })
                .catch(error => {
                    self.loading = false;
                });
        },
        reload() {
            this.fetch();
        },
        async click(id, type) {
            switch (type) {
                case "RouterPush":
                    this.$router.push({
                        name: "CompanyDetails",
                        params: { id: id }
                    });
                    break;
                case "Delete":
                    if (
                        await this.$refs.dialog.open(
                            "Confirm",
                            "Are you sure you want to delete this record?"
                        )
                    ) {
                        let self = this;
                        this.$store
                            .dispatch("company/delete", { id: id })
                            .then(response => {
                                self.loading = false;
                                self.fetch();
                            })
                            .catch(error => {
                                self.loading = false;
                            });
                    }
                    break;
            }
            // console.log(id, key);
        }
    }
};
</script>
